<template>
  <div class="xbsjselect" ref="opens">
    <div class="xbsjselect-selectdiv" @click="selectClick">

      <span class="xbsjselect-selectText" v-if="!currentValue && list[0].value !='BNB/USDT'">全部</span>
      <span class="xbsjselect-selectText" v-else-if="list[0].value =='BNB/USDT' && !currentValue" :class="status?'selectText':''" >BNB/USDT</span>
      <span class="xbsjselect-selectText" v-else :class="status?'selectText':''">{{language[currentValue] }}</span>

      
      <button class="xbsjselect-selectButton" :class="[rotate?'go':'to']"></button>
    </div>
    <ul class="xbsjselect-selectOption" v-show="selectshow">
      <li v-for="(ct,index) in listdata" :key="index" @click="selectName(ct.label,ct.value)">
        <span>{{ language [ct.value] }}</span>
      </li>
    </ul>
  </div>
</template>


<script>
import {mapGetters} from "vuex";
export default {
  props: {
    list: {
        type:Array,
    },
  },
  data() {
    return {
      selectshow: false,
      currentValue:this.value,
       rotate:false,
    };
  },
  computed: {
    ...mapGetters(['language']),
    listdata() {
      return this.list;
    }
  },
  mounted(){
      document.addEventListener("click", (e) => {
      if(this.$refs && this.$refs.opens){
         if(!this.$refs.opens.contains(e.target)){
            this.selectshow = false;
       } 
      }
    });
  },
  methods: {
    selectClick() {
      this.selectshow = !this.selectshow;
      this.rotate=!this.rotate;
    },
    selectName(data,value) {
      this.currentValue = value;
      this.selectshow = false;
      this.rotate=!this.rotate;
       this.$emit("input",data);
    }
  },
};
</script>

