<script>
import MTabs from "@/components/mTabs";
import { optionGraphql } from "@/common/api";
import { mapGetters, mapState, mapActions } from "vuex";
import { BN } from "@/common/util";
import { blockConfig } from "@/common/walletConfig";
import MButton from "@/components/mButton";
import flSelect from "@/components/flSelect";

export default {
  name: "optionRecord",
  components: {
    MButton,
    MTabs,
    flSelect,
  },
  computed: {
    ...mapState("app", ["locale"]),
    ...mapGetters(["language", "currentRound"]),
    ...mapState("app", ["address", "device"]),
    ...mapState("option", ["currentRounds", "contractName"]),
    convertData() {
      let arr = [];
      this.data.map((row) => {
        // let {endBlock, failed} = row.round;
        arr.push(this.convertor(row));
      });
      return arr;
    },
  },
  watch: {
    currentRounds() {
      this.getList();
    },
    locale(newName) {
      if (
        newName == "de_DE" ||
        newName == "fr" ||
        newName == "es" ||
        newName == "en"
      ) {
        this.listeningLanguage = true;
      } else {
        this.listeningLanguage = false;
      }
    },
  },
  data() {
    return {
      dataSource: [
        {
          label: 1,
          value: "BNB/USDT",
        },
        {
          label: 2,
          value: "ETH/USDT",
        },
        {
          label: 3,
          value: "BNB/DCOIN",
        },
      ],
      dataSource1: [
        {
          label: 1,
          value: "resolution_5",
        },
        {
          label: 2,
          value: "resolution_15",
        },
      ],

      dataSource2: [
        {
          label: 1,
          value: "down",
        },
        {
          label: 2,
          value: "up",
        },
      ],

      listeningLanguage: false,
      tabIndex: 0,
      tabs: [
        { type: 0, language_key: "order_unclaimed" },
        { type: 1, language_key: "order_claimed" },
      ],
      data: [],
      loading: false, //加载中
      openFlag: {},
      roundMap: {
        5: "5M",
        15: "15M",
      },
      fixDom: 10,
      win: 10,
      betTokenSymbol: "BNB_USDT",
      roundType: "",
      position: "",
    };
  },
  beforeMount() {
    let newName = this.locale;
    if (
      newName == "de_DE" ||
      newName == "fr" ||
      newName == "es" ||
      newName == "en"
    ) {
      this.listeningLanguage = true;
    }
    // roundType:5,position:"Bull"
    let symbol = 'betTokenSymbol:"BNB_USDT"';
    this.getList(symbol);
  },
  methods: {
    ...mapActions("option", ["setContractName"]),
    convertor(row) {
      let { round, amount, createdAt, position } = row;
      let {
        totalAmount,
        lockPrice,
        closePrice,
        rewardAmount,
        rewardBaseCalAmount,
      } = round;
      amount = new BN(amount);
      totalAmount = new BN(totalAmount).myFixed();
      lockPrice = new BN(lockPrice || 0);
      closePrice = new BN(closePrice || 0);
      rewardAmount = new BN(rewardAmount || 0);
      rewardBaseCalAmount = new BN(rewardBaseCalAmount || 0);
      return {
        ...row,
        amount: amount.myFixed(),
        round: {
          ...round,
          totalAmount,
          lockPrice: lockPrice.eq(0) ? "????" : lockPrice.myFixed(),
          closePrice: closePrice.eq(0) ? "????" : closePrice.myFixed(),
        },
        delta: closePrice.minus(lockPrice),
        isWinner: round.position === position,
        gain:
          round.position === position
            ? amount.times(rewardAmount).div(rewardBaseCalAmount).myFixed()
            : "0",
        // gain:
        //   round.position === position
        //     ? amount.times(rewardAmount).div(rewardBaseCalAmount).myFixed()
        //     : this.language.nft_fragment,
        createdAt: new Date(Number(createdAt + "000")).format("yyyy-MM-dd"),
      };
    },
    getQuery(data) {
      let tq;
      switch (this.tabIndex) {
        case 0:
          tq = `claimed:false`;
          break;
        case 1:
          tq = "claimed:true";
          break;
        default:
          tq = "";
      }
      // {}
      return `query{
        bets(first:300,orderBy:block,orderDirection:desc,where:{user:"${this.address}",${data},${tq}}) {
          id
          symbol
          round {
            id
            epoch
            totalAmount
            lockPrice
            closePrice
            startHash
            endHash
            position
            endBlock
            failed
            rewardAmount
            rewardBaseCalAmount
          }
          roundType
          amount
          position
          claimed
          nftTokenId
          createdAt
        }
      }`;
    },
    getList(symbol) {
      if (this.loading) return;
      this.loading = true;
      optionGraphql(this.getQuery(symbol))
        .then(({ bets }) => {
          this.data = bets;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    tabChangeHandle() {
      let roundType = this.roundType ? `,roundType:${this.roundType}` : "";
      let position = this.position ? `,position:"${this.position}"` : "";
      let str = `betTokenSymbol:"${this.betTokenSymbol}"${roundType}${position}`;
      this.getList(str);
    },
    claim(roundId, resolution, symbol, $el) {
      // console.log(roundId, resolution, symbol, $el,this.contractName)
      $el.setLoading(true);
      // let contractName = symbolContractMapping[symbol];
      this.$api
        .claimReward(roundId, resolution, this.contractName)
        .then(() => {
          $el.setLoading(false);
          this.$api.getBalance();
          this.deleteRow(roundId);
        })
        .catch(() => {
          $el.setLoading(false);
        });
    },
    deleteRow(epoch) {
      let data = this.data;
      let idx = data.findIndex(({ round }) => round.epoch === epoch);
      idx !== -1 && data.splice(idx, 1);
    },
    openBlock(hash) {
      window.open(blockConfig.host + hash);
    },
    currencyFn(data) {
      if (data == "3") {
        this.betTokenSymbol = "BNB_DCOIN";
        this.setContractName("DcionPricePrediction");
      } else if (data == "2") {
        this.betTokenSymbol = "ETH_USDT";
        this.setContractName("EthPricePrediction");
      } else {
        this.betTokenSymbol = "BNB_USDT";
        this.setContractName("BnbPricePrediction");
      }
      let roundType = this.roundType ? `,roundType:${this.roundType}` : "";
      let position = this.position ? `,position:"${this.position}"` : "";
      let str = `betTokenSymbol:"${this.betTokenSymbol}"${roundType}${position}`;
      this.getList(str);
    },
    minuteFn(data) {
      let str = "";
      if (data == "1") {
        str = "5";
      } else {
        str = "15";
      }
      this.roundType = str;
      let roundType = this.roundType ? `,roundType:${this.roundType}` : "";
      let position = this.position ? `,position:"${this.position}"` : "";
      let symbol = `betTokenSymbol:"${this.betTokenSymbol}"${roundType}${position}`;
      this.getList(symbol);
    },
    LookFn(data) {
      let str = "";
      if (data == "2") {
        str = "Bull";
      } else {
        str = "Bear";
      }
      this.position = str;
      let roundType = this.roundType ? `,roundType:${this.roundType}` : "";
      let position = this.position ? `,position:"${this.position}"` : "";
      let symbol = `betTokenSymbol:"${this.betTokenSymbol}"${roundType}${position}`;
      this.getList(symbol);
    },
  },
};
</script>

